














.title-form {
    text-align: center;
    font-size: 55px;
    margin: 80px 50px;
}

.title-form span {
    color: rgb(156,14,156);
}

.container-form {
    padding: 10px;
    width: 80%;
    max-width: 900px;
    margin: 30px auto 0;
    display: flex;
    flex-direction: column;
}

.container-form label {
    font-size: 20px;
    margin: 5px 0;
}

.container-form input, .container-form textarea {
    padding: 10px;
    margin-bottom: 25px;
    font-size: 18px;
    border-radius: 10px;
}

.container-form textarea {
    min-height: 20px;
    font-family: Arial, Helvetica, sans-serif;
}

.container-form button {
    font-size: 15px;
    padding: 10px 15px;
    width: 30%;
    background: transparent;
    border: 1px solid rgba(0, 0, 0, 0.8);
    border-radius: 20px;
    cursor: pointer;
    margin: 0 35%;
    color: #f1f1f1;
    background: linear-gradient(45deg, rgba(156,14,156, 0.8), rgba(25,25,112,0.8));
}